import { handleNetworkError } from '@/order/lib/network-error';
import { fetchGraphQl } from '@/order/api/fetch-graphql';
import { GET_CUSTOMER_ORDERS_RETURN } from './graphql/getCustomerOrdersReturn.graphql';
import { transformCustomerOrdersReturns } from '@/order/data/transforms';
import { CustomerOrdersReturnModel } from '@/order/data/models';
import { GetCustomerOrdersReturnResponse } from '@/order/types';

export const getCustomerOrdersReturn = async (
  pageSize = 10,
  currentPage = 1
): Promise<CustomerOrdersReturnModel | null> => {
  return await fetchGraphQl(GET_CUSTOMER_ORDERS_RETURN, {
    method: 'GET',
    cache: 'force-cache',
    variables: { pageSize, currentPage },
  })
    .then((response: GetCustomerOrdersReturnResponse) => {
      return transformCustomerOrdersReturns(response?.data?.customer.returns);
    })
    .catch(handleNetworkError);
};
